.parent-login {
	 height: 100vh;
	 width: 100vw;
	 background: url('https://source.unsplash.com/2560x1440/daily') center/cover fixed;
	 display: flex;
	 justify-content: center;
	 align-items: center;
}
 #formContainer {
	 display: flex;
	 transition: 0.2s ease;
	 height: 342.5px;
	 transition-delay: 0.3s;
}
 #formContainer.toggle {
	 height: 480px;
	 transition-delay: 0s;
}
 .formLeft {
	 background: #fff;
	 border-radius: 5px 0 0 5px;
	 padding: 0 35px;
	 box-sizing: border-box;
	 display: flex;
	 align-items: center;
}
 .formLeft img {
	 display: block;
	 width: 72px;
	 border-radius: 50%;
	 box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
}
 .formRight {
	 position: relative;
	 overflow: hidden;
	 border-radius: 0 5px 5px 0;
	 display: flex;
	 flex-direction: column;
	 justify-content: center;
}
 .formRight:before {
	 content: "";
	 position: absolute;
	 top: -10px;
	 left: -10px;
	 width: calc(100% + 20px);
	 height: calc(100% + 20px);
	 background: url('https://source.unsplash.com/2560x1440/daily') center/cover fixed;
	 box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
	 -webkit-filter: blur(5px);
	         filter: blur(5px);
}
 .formRight form {
	 position: relative;
	 width: 350px;
	 padding: 25px;
	 box-sizing: border-box;
	 white-space: nowrap;
	 overflow: hidden;
	 background: #000;
}
 .formRight form header {
	 color: #fff;
	 text-align: center;
	 margin-bottom: 15px;
}
 .formRight form header h1 {
	 margin: 0;
	 font-weight: 400;
	 -webkit-user-select: none;
	     -ms-user-select: none;
	         user-select: none;
}
 .formRight form header p {
	 margin: 5px 0 0;
	 opacity: 0.5;
	 font-size: 14px;
	 -webkit-user-select: none;
	     -ms-user-select: none;
	         user-select: none;
}
 .formRight form section label {
	 display: block;
	 margin-bottom: 15px;
	 position: relative;
}
 .formRight form section label p {
	 color: #fff;
	 margin: 0 0 10px 0;
	 font-weight: 600;
	 font-size: 12px;
	 opacity: 0.5;
	 -webkit-user-select: none;
	     -ms-user-select: none;
	         user-select: none;
}
 .formRight form section label input {
	 width: 100%;
	 display: block;
	 border: none;
	 background: transparent;
	 color: #fff;
	 border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	 padding: 0 0 10px;
	 box-sizing: border-box;
	 font-weight: 600;
}
 .formRight form section label input:focus ~ .border {
	 transform: scale(1, 1);
}
 .formRight form section label input:not(:-ms-input-placeholder) ~ .border {
	 transform: scale(1, 1);
}
 .formRight form section label input:not(:placeholder-shown) ~ .border {
	 transform: scale(1, 1);
}
 .formRight form section label .border {
	 position: absolute;
	 bottom: 0;
	 left: 0;
	 width: 100%;
	 height: 2px;
	 background: #fff;
	 transform: scale(0, 1);
	 transition: 0.2s ease;
}
 .formRight form section label:last-child {
	 margin-bottom: 0;
}
 .formRight form section button {
	 background: #00897b;
	 border: none;
	 width: 100%;
	 padding: 10px 0;
	 font-weight: 600;
	 color: #fff;
	 cursor: pointer;
}
 .formRight form section button:hover {
	 background: #007f72;
}
 .formRight form footer {
	 margin-top: 15px;
	 display: flex;
}
 .formRight form footer button {
	 background: transparent;
	 padding: 0;
	 border: none;
	 color: #fff;
	 cursor: pointer;
	 font-size: 12px;
	 font-weight: bold;
	 flex: 1 1;
	 opacity: 0.5;
}
 .formRight form footer button:hover {
	 opacity: 1;
}
 .formRight form.otherForm {
	 top: 0;
	 left: 0;
	 position: absolute;
	 background: #000;
	 height: 100%;
	 z-index: 1;
	 display: flex;
	 flex-direction: column;
	 justify-content: center;
	 width: 0;
	 padding: 25px 0;
	 transition: 0.2s ease;
	 transition-delay: 0.2s;
	 border-left: 1px solid rgba(0, 0, 0, 0.1);
}
 .formRight form.otherForm header {
	 color: #fff;
	 opacity: 0;
	 transition: 0.2s ease;
	 transition-delay: 0s;
}
 .formRight form.otherForm p {
	 color: #fff;
}
 .formRight form.otherForm section {
	 opacity: 0;
	 transition: 0.2s ease;
	 transition-delay: 0s;
}
 .formRight form.otherForm footer {
	 border-top-color: rgba(0, 0, 0, 0.1);
	 opacity: 0;
}
 .formRight form.otherForm footer button {
	 color: #fff;
}
 .formRight form.otherForm input {
	 border-color: #1A1A1A;
	 color: #fff;
}
 .formRight form.otherForm .border {
	 background: #000;
}
 .formRight form.otherForm.toggle {
	 width: 100%;
	 padding: 25px;
	 transition-delay: 0s;
}
 .formRight form.otherForm.toggle header, .formRight form.otherForm.toggle section, .formRight form.otherForm.toggle footer {
	 opacity: 1;
	 transition-delay: 0.3s;
}
 
.parent-loader {
  height: 100%;
  background-image: radial-gradient(
    circle farthest-corner at center,
    #000 0%,
    #000 100%
  );
}

.loader {
  position: absolute;
  top: calc(50% - 32px);
  left: calc(50% - 32px);
  width: 64px;
  height: 64px;
  border-radius: 50%;
  perspective: 800px;
}

.inner {
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.inner.one {
  left: 0%;
  top: 0%;
  animation: rotate-one 1s linear infinite;
  border-bottom: 3px solid #efeffa;
}

.inner.two {
  right: 0%;
  top: 0%;
  animation: rotate-two 1s linear infinite;
  border-right: 3px solid #efeffa;
}

.inner.three {
  right: 0%;
  bottom: 0%;
  animation: rotate-three 1s linear infinite;
  border-top: 3px solid #efeffa;
}

@keyframes rotate-one {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }
  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@keyframes rotate-two {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }
  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@keyframes rotate-three {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }
  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}

