.round-checkbox {
    display: block;
    position: absolute;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    top: 6px;
    right: 0px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Create a custom checkbox */
.round-checkbox .checkmark {
    position: absolute;
    top: 25px;
    right: 20px;
    height: 25px;
    width: 25px;
    border-radius: 25px;
    border: 2px solid #0FCE8F;
}

/* Create the checkmark/indicator (hidden when not checked) */
.round-checkbox .checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.checkmark.checked:after {
    display: block;
}

/* Style the checkmark/indicator */
.round-checkbox .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid #0FCE8F;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}


